import PropTypes from 'prop-types';
import {useEffect} from 'react';
import { useSnackbar } from 'notistack';
// form
import { useForm } from 'react-hook-form';
// @mui
import { Stack, Card, InputAdornment } from '@mui/material';
import { LoadingButton } from '@mui/lab';
// components
import Iconify from '../../../../components/Iconify';
import { FormProvider, RHFTextField } from '../../../../components/hook-form';
import {useDispatch,useSelector,store} from '../../../../redux/store';
import {setAccountSocialLinks,loadAccount}  from '../../../../redux/slices/account';

//

// ----------------------------------------------------------------------

const SOCIAL_LINKS = [
  {
    value: 'facebookLink',
    icon: <Iconify icon={'eva:facebook-fill'} width={24} height={24} />,
  },
  {
    value: 'instagramLink',
    icon: <Iconify icon={'ant-design:instagram-filled'} width={24} height={24} />,
  },
  {
    value: 'linkedinLink',
    icon: <Iconify icon={'eva:linkedin-fill'} width={24} height={24} />,
  },
  {
    value: 'twitterLink',
    icon: <Iconify icon={'eva:twitter-fill'} width={24} height={24} />,
  },
];

// ----------------------------------------------------------------------


export default function AccountSocialLinks() {
  const { enqueueSnackbar } = useSnackbar();
  const dispatch = useDispatch();
  const {account, isLoading} = useSelector((state)=>state.account);
  const defaultValues = {
    facebookLink: (account && account.socialLink&& account.socialLink.facebookLink)?account.socialLink.facebookLink :'',
    instagramLink: (account && account.socialLink&& account.socialLink.instagramLink)?account.socialLink.instagramLink :'',
    linkedinLink: (account && account.socialLink&& account.socialLink.linkedinLink)?account.socialLink.linkedinLink :'',
    twitterLink: (account && account.socialLink&& account.socialLink.twitterLink)?account.socialLink.twitterLink : '',
  };

  const methods = useForm({
    defaultValues,
  });

  const {
    handleSubmit,
    reset,
    formState: { isSubmitting },
  } = methods;

  const onSubmit = async (data) => {
    try {
      dispatch(setAccountSocialLinks(data,enqueueSnackbar));
    } catch (error) {
      console.error(error);
    }
  };
  useEffect(()=>{
    dispatch(loadAccount());
  },[dispatch]);
  useEffect(()=>{
    if(account && account.email){
      reset(defaultValues);
      console.log(defaultValues);
    }
  },[account]);
  return (
    <Card sx={{ p: 3 }}>
      <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
        <Stack spacing={3} alignItems="flex-end">
          {SOCIAL_LINKS.map((link,index) => (
            <RHFTextField
              key={index}
              name={link.value}
              InputProps={{
                startAdornment: <InputAdornment position="start">{link.icon}</InputAdornment>,
              }}
            />
          ))}

          <LoadingButton type="submit" variant="contained" loading={isLoading}>
            Save Changes
          </LoadingButton>
        </Stack>
      </FormProvider>
    </Card>
  );
}
